<template>
  <div v-if="isReady" class="print-page__wrapper" id="print-page">
    <div class="print-head">
      <img src="../../assets/svg/logo.svg" class="logo" alt="aha logo" />
      <address>
        Netgengið ehf.
        <br />Skútuvogi 12A <br />104 Reykjavík <br />KT: 650311-1160 <br />VSK:
        107580
      </address>
    </div>
    <div class="print-page__header">
      <div class="print-page__header-info">
        <h1 class="print-page__header-title">
          {{ $t("orderTitle") }}{{ ` #${ordersInfo.mainOrderIncId}` }}
        </h1>
      </div>
      <div class="print-page__header-order-date">
        {{ $t("orderDate") }}: {{ ordersInfo.orderDate | formatDateTime }}
      </div>
    </div>

    <OrdersDetailInformation
      :billingAddress="billingAddress"
      :paymentInfo="paymentInfo"
    />
    <OrdersDetailSubOrders :subOrders="subOrders" />
    <OrdersDetailSummary
      v-if="!ordersDetailProps.isInvoices"
      :ordersSummary="ordersSummary"
    />
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import OrdersDetailInformation from "@/components/ViewOrdersDetail/OrdersDetailInformation";
import OrdersDetailSubOrders from "@/components/ViewOrdersDetail/OrdersDetailSubOrders";
import OrdersDetailSummary from "@/components/ViewOrdersDetail/OrdersDetailSummary";

import getStatusColor from "@/util/getStatusColor";

export default {
  name: "ViewPrintOrders",
  components: {
    OrdersDetailInformation,
    OrdersDetailSubOrders,
    OrdersDetailSummary,
  },
  data() {
    return {
      isReady: false,
      orderId: undefined,
      ordersInfo: undefined,
      billingAddress: undefined,
      paymentInfo: undefined,
      subOrders: undefined,
      ordersSummary: undefined,
    };
  },
  computed: {
    ...mapGetters(["printData"]),
    ordersDetailProps() {
      return {
        subPath: "",
        isInvoices: false,
      };
    },
  },
  methods: {
    statusColor(status) {
      return getStatusColor(status);
    },
  },
  mounted() {
    const printData = this.printData;
    if (printData) {
      const {
        orderId,
        ordersInfo,
        billingAddress,
        paymentInfo,
        subOrders,
        ordersSummary,
      } = printData;
      this.orderId = orderId;
      this.ordersInfo = ordersInfo;
      this.billingAddress = billingAddress;
      this.paymentInfo = paymentInfo;
      this.subOrders = subOrders;
      this.ordersSummary = ordersSummary;
      this.isReady = true;
      window.print();
    }
  },
};
</script>

